import renderHTML from 'react-render-html';
import ContentEditable from 'react-contenteditable';
import _merge from "lodash.merge";

let addedClass= 'mdadded',modifiedClass='mdmodified',removedClass='mdremoved';
const {__} = wp.i18n;  // eslint-disable-line
const isSafari = /^((?!chrome|android).)*safari/i.test(navigator.userAgent);

export default class SuggestionComment extends React.Component {

  constructor(props) {

    super(props);

    this.contentEditable = React.createRef();
    this.editComment = this.editComment.bind(this);
    this.saveComment = this.saveComment.bind(this);
    this.removeComment = this.removeComment.bind(this);
    this.cancelEdit = this.cancelEdit.bind(this);
    this.removeSuggestion = this.removeSuggestion.bind(this);
    this.acceptSuggestion = this.acceptSuggestion.bind(this);
    this.updateBlockContentChanges = this.updateBlockContentChanges.bind(this); 
    this.state = {editing: false, contentHtml: '<br/>'};

    /**dynamic user data */
    this.state = { 
      currentUserid: getCurrentUserId(),
      isLoaded: false,
    };  

  } 

  editComment() {
     this.setState({editing: true});
 
     // Handling edited value.
     var editedValue = this.props.children[0]; 

     editedValue =  removeLinkFromEditableText(editedValue);
   
     this.state.contentHtml = editedValue;
  }

  saveComment(event) {
   
    var previousValue = this.props.children[0]; 
    // Filtering anchor tag and return the url text only.
    // For bug fixing of semicolon there is a little chnage in regex
    previousValue =  removeLinkFromEditableText(previousValue);
    
    var newText = this.state.contentHtml;
    if(jQuery(event.currentTarget).hasClass('btn-disabled')){
      return false;
    }
 
    newText = filterTextBeforeSave(newText);

    let newTextstring =  newText.replace( /<br>/igm, '' );
    let newTextstringFlag = /\S/g.test(newTextstring);

    if (!newTextstringFlag) {
      wp.data.dispatch( 'core/notices' ).createNotice(
        'error', // Can be one of: success, info, warning, error.
        __('Please write a comment to share!'),// Text string to display.
        {
            id: 'commenttoshare',
            isDismissible: true, // Whether the user can dismiss the notice.
            // Any actions the user can perform. 
        }
      );
      // alert( "Please write a comment to share!" );
      return false;
    }

    if ( newText === previousValue ) {
      wp.data.dispatch( 'core/notices' ).createNotice(
        'error', // Can be one of: success, info, warning, error.
        __('Please edit the text before save!'),// Text string to display.
        {
            id: 'commenttoshare',
            isDismissible: true, // Whether the user can dismiss the notice.
            // Any actions the user can perform. 
        }
      );
      // alert( "Please write a comment to share!" );
      return false;
    }    

    this.props.updateCommentFromBoard( newText, this.props.index ); 
    this.setState({editing: false})
  }

  removeComment(event) { 

    this.props.removeCommentFromBoard( this.props.index );
    jQuery(".comment-delete-overlay").removeClass("show");
    
  }

  cancelEdit() { 
    this.setState({editing: false}); 
  } 

  removeSuggestion() {
   
    const { suggestionID } = this.props;
    let { clientId } = this.props;
    const userId = wp.data.select('core').getCurrentUser().id;
    const timestamp=getTimestampWithTimezone();
    let content;

    let blockAttributes = wp.data.select('core/block-editor').getBlockAttributes(clientId);
    if(blockAttributes === null && suggestionID !== null) {
       clientId = jQuery('#' + suggestionID).parents('.wp-block')[0].getAttribute('data-block');  
       blockAttributes = wp.data.select('core/block-editor').getBlockAttributes(clientId);
    }

    
    if ( undefined !== blockAttributes.content ) {
      content = blockAttributes.content;
    } else if ( undefined !== blockAttributes.caption ) {
      content = blockAttributes.caption;
    } else {
      content = blockAttributes.values;
    }
    
    if ('' !== content ) {
      let suggestionHistory = wp.data.select( 'core/editor' ).getEditedPostAttribute( 'meta' )['_sb_suggestion_history'];
      if ( 0 < suggestionHistory.length ) {
        suggestionHistory = JSON.parse(suggestionHistory);
        let findItem = 'id="' + suggestionID + '"';
        if ( suggestionHistory[suggestionID] && -1 !== content.indexOf(findItem) ) {
          let suggestionDetail = suggestionHistory[suggestionID][0];
          let mode = suggestionHistory[suggestionID][0].mode;
          let action = suggestionHistory[suggestionID][0].action;
          let tempDiv = document.createElement('div');
          let childElements; 
          tempDiv.innerHTML = content;  // phpcs:ignore 
          if('add' === mode.toLowerCase()){
            childElements =tempDiv.getElementsByClassName(addedClass) ;
          }else if('edit' === mode.toLowerCase()){
            childElements =tempDiv.getElementsByClassName(modifiedClass);  
          }else{
            childElements =tempDiv.getElementsByClassName(removedClass);
          }
     
          for ( let i = 0; i < childElements.length; i++ ) {
            if ( undefined !== childElements[i].id && suggestionID === childElements[i].id ) {

              if( suggestionDetail['is_block_align'] ){ 
                childElements[i].parentNode.removeChild(childElements[i]);
                if(suggestionDetail['oldvalue'] !== undefined || suggestionDetail['oldvalue'] !== ''){
                    wp.data.dispatch( 'core/block-editor' ).updateBlock( clientId, { attributes: {   align:  suggestionDetail['oldvalue'] } });
                }else{
                    wp.data.dispatch( 'core/block-editor' ).updateBlock( clientId, { attributes: {   align: '' } });
                }
              }

              if ( 'add' === mode.toLowerCase() ||  'edit' === mode.toLowerCase() ) {
                if ( 'format' === action.toLowerCase() ||  'link' === action.toLowerCase() ) {
                  let currentElement =jQuery( '#' + suggestionID, tempDiv )[0];
                   
                  if( suggestionDetail['is_element'] && suggestionDetail['oldvalue']!==undefined && !suggestionDetail['is_attr_update'] ){ 
                       jQuery( '#' + suggestionID, tempDiv ).replaceWith( suggestionDetail['oldvalue'] );  // phpcs:ignore
                  }else if( suggestionDetail['is_attr_update'] && suggestionDetail['attrs_old'] && suggestionDetail['attrs_new'] ){
                        
                    if( currentElement.nodeName.toLowerCase() === 'ins' ||
                    currentElement.nodeName.toLowerCase() === 'del' 
                    ){
                      currentElement = currentElement.firstChild;     
                    }  

                    for (const [key, value] of Object.entries(suggestionDetail['attrs_new'])) {
                      currentElement.removeAttribute(key);
                    } 
                      
                    for (const [key, value] of Object.entries(suggestionDetail['attrs_old'])) {
                      currentElement.setAttribute(key, value);
                    }  
                    jQuery( '#' + suggestionID, tempDiv ).replaceWith(  currentElement.outerHTML   );  // phpcs:ignore                
                  }               
                } else {
                  childElements[i].parentNode.removeChild(childElements[i]); 
                }
              } else if( 'delete' === mode.toLowerCase()  && (suggestionDetail['is_block_remove'] || suggestionDetail['is_block_align'] ) ){
                jQuery( '#' + suggestionID, tempDiv ).remove();
              } else {
                jQuery( '#' + suggestionID, tempDiv ).replaceWith(  childElements[i].innerHTML  );  // phpcs:ignore
              }

              // save status for activity center log
              suggestionHistory[suggestionID][0]['status']={'action':'reject','user':userId,'timestamp':timestamp};
              suggestionHistory[suggestionID][0]['is_draft']=true; 
              suggestionHistory[suggestionID][0]['updated_at']=timestamp;
              
              let finalContent = tempDiv.innerHTML;
              this.updateBlockContentChanges( clientId );
              
              if ( undefined !== blockAttributes.content ) {
                wp.data.dispatch( 'core/block-editor' ).updateBlock( clientId, {
                  attributes: {
                    content: finalContent
                  }
                });
              } else if ( undefined !== blockAttributes.caption ) {
                wp.data.dispatch( 'core/block-editor' ).updateBlock( clientId, {
                  attributes: {
                    caption: finalContent
                  }
                });
              } else {
                wp.data.dispatch( 'core/block-editor' ).updateBlock( clientId, {
                  attributes: {
                    values: finalContent
                  }
                });
              }
             
             
              document.getElementById('sg' + suggestionID ).remove();
              wp.data.dispatch('core/editor').editPost({meta: {_sb_suggestion_history: JSON.stringify(suggestionHistory) } });
              this.updatePostMeta();
              jQuery(`#cf-sg${suggestionID}`).removeClass('active');
              jQuery('#md-span-comments .cls-board-outer').removeAttr('style');
              jQuery('#md-span-comments .cls-board-outer').removeClass('focus');
              jQuery('#md-span-comments .cls-board-outer').removeClass('is-open'); 
              break;
            }
          }
        }
      }
    } 
    jQuery.event.trigger({ type: "onBlockLevelSuggestionUpdate"});
  }
 
  updateBlockContentChanges( clientId ) {
     if ( '' !== clientId ) {
      let updateBlockChanges = wp.data.select( 'core/editor' ).getEditedPostAttribute( 'meta' )['_sb_update_block_changes'];

      if ( 0 < updateBlockChanges.length ) {
        updateBlockChanges = JSON.parse(updateBlockChanges);
      }

      if ( 0 === updateBlockChanges.length) {
        updateBlockChanges = {};
        updateBlockChanges[clientId] = true;
      } else if ( ! updateBlockChanges[clientId] ) {
        updateBlockChanges[clientId] = true;
      }
      wp.data.dispatch('core/editor').editPost({meta: {_sb_update_block_changes: JSON.stringify(updateBlockChanges) } });
    }
  }
 
  acceptSuggestion() {
    const { suggestionID } = this.props;
    let { clientId } = this.props;
    const userId = wp.data.select('core').getCurrentUser().id;
    const timestamp=getTimestampWithTimezone();
    let content;

    let blockAttributes = wp.data.select('core/block-editor').getBlockAttributes(clientId);
    if(blockAttributes === null && suggestionID !== null) {
       clientId = jQuery('#' + suggestionID).parents('.wp-block')[0].getAttribute('data-block');  
       blockAttributes = wp.data.select('core/block-editor').getBlockAttributes(clientId);
    }
    
    if ( undefined !== blockAttributes.content ) {
      content = blockAttributes.content;
    } else if ( undefined !== blockAttributes.caption ) {
      content = blockAttributes.caption;
    } else {
      content = blockAttributes.values;
    }
    if ('' !== content ) {
      let suggestionHistory = wp.data.select( 'core/editor' ).getEditedPostAttribute( 'meta' )['_sb_suggestion_history'];
      if ( 0 < suggestionHistory.length ) {
        suggestionHistory = JSON.parse(suggestionHistory);
        let findItem = 'id="' + suggestionID + '"';
        if ( suggestionHistory[suggestionID] && -1 !== content.indexOf(findItem) ) {
          let suggestionDetail = suggestionHistory[suggestionID][0];
          let mode = suggestionHistory[suggestionID][0].mode;
          let action = suggestionHistory[suggestionID][0].action;
          let tempDiv = document.createElement('div');
          tempDiv.innerHTML = content;  // phpcs:ignore
          let childElements;
        
          if('add' === mode.toLowerCase()){
            childElements =tempDiv.getElementsByClassName(addedClass) ;
          }else if('edit' === mode.toLowerCase()){
            childElements =tempDiv.getElementsByClassName(modifiedClass);  
          }else{
            childElements =tempDiv.getElementsByClassName(removedClass);
          }

          for ( let i = 0; i < childElements.length; i++ ) {
            if ( undefined !== childElements[i].id && suggestionID === childElements[i].id ) { 
              if ( 'add' === mode.toLowerCase() ||  'edit' === mode.toLowerCase() ) {
                if ( 'format' === action.toLowerCase() ||  'link' === action.toLowerCase() ) {
                  let currentElement =jQuery( '#' + suggestionID, tempDiv )[0];
                  if( suggestionDetail['is_block_align']){
                    
                    childElements[i].parentNode.removeChild(childElements[i]);
                    
                  }else if( suggestionDetail['is_element'] && suggestionDetail['newvalue'] && !suggestionDetail['is_attr_update'] ){ 
                       jQuery( '#' + suggestionID, tempDiv ).replaceWith( suggestionDetail['newvalue'] );  // phpcs:ignore 
                  }else if( suggestionDetail['is_attr_update'] ){
                    if( currentElement.nodeName.toLowerCase() === 'ins' ||
                        currentElement.nodeName.toLowerCase() === 'del' 
                    ){
                      currentElement = currentElement.firstChild;     
                    }   
                    for (const [key, value] of Object.entries(suggestionDetail['attrs_old'])) {
                      currentElement.removeAttribute(key);
                    } 
                      
                    for (const [key, value] of Object.entries(suggestionDetail['attrs_new'])) {
                      currentElement.setAttribute(key, value);
                    }  
                   
                    jQuery( '#' + suggestionID, tempDiv ).replaceWith( currentElement.outerHTML );  // phpcs:ignore                
                  }  
                } else {
                  jQuery( '#' + suggestionID, tempDiv ).replaceWith( childElements[i].innerHTML );  // phpcs:ignore 
                }
              } else if( 'delete' === mode.toLowerCase()  && suggestionDetail['is_block_remove'] ){
                 var removeBlockclientId=jQuery( '#' + suggestionID).parent().attr('data-block'); 
                 if(removeBlockclientId){
                  wp.data.dispatch( 'core/editor' ).editPost({meta: {_sb_is_suggestion_mode: false}}); 
                  wp.data.dispatch( 'core/editor' ).removeBlock(removeBlockclientId);
                  wp.data.dispatch( 'core/editor' ).editPost({meta: {_sb_is_suggestion_mode: true}});
                 }  
              }else { 
                  childElements[i].parentNode.removeChild(childElements[i]); 
              }

              // save status for activity center log
              suggestionHistory[suggestionID][0]['status']={'action':'accept','user':userId,'timestamp':timestamp};
              suggestionHistory[suggestionID][0]['is_draft']=true; 
              suggestionHistory[suggestionID][0]['updated_at']=timestamp;

              let finalContent = tempDiv.innerHTML;
              this.updateBlockContentChanges( clientId );

              if ( undefined !== blockAttributes.content ) {
                wp.data.dispatch( 'core/block-editor' ).updateBlock( clientId, {
                  attributes: {
                    content: finalContent
                  }
                });
              } else if ( undefined !== blockAttributes.caption ) {
                wp.data.dispatch( 'core/block-editor' ).updateBlock( clientId, {
                  attributes: {
                    caption: finalContent
                  }
                });
              } else {
                wp.data.dispatch( 'core/block-editor' ).updateBlock( clientId, {
                  attributes: {
                    values: finalContent
                  }
                });
              }
              
            
              
              document.getElementById('sg' + suggestionID ).remove();
              wp.data.dispatch('core/editor').editPost({meta: {_sb_suggestion_history: JSON.stringify(suggestionHistory) } });
              this.updatePostMeta();
              jQuery(`#cf-sg${suggestionID}`).removeClass('active');
              jQuery('#md-span-comments .cls-board-outer').removeAttr('style');
              jQuery('#md-span-comments .cls-board-outer').removeClass('focus');
              jQuery('#md-span-comments .cls-board-outer').removeClass('is-open');
               
              break;
            }
          }
        }
      }
    }
    jQuery.event.trigger({ type: "onBlockLevelSuggestionUpdate"});  
  }

  updatePostMeta(){
    // update post suggestion history meta on autosave
    var isAutosavingPost  = wp.data.select('core/editor').isAutosavingPost();
    let suggestionHistory = wp.data.select('core/editor').getEditedPostAttribute('meta')['_sb_suggestion_history'];
      
    if (!isAutosavingPost && suggestionHistory !== undefined ) {
        
        var data = {
          'action': 'sg_update_suggestion_history',
          'currentPostID': wp.data.select('core/editor').getCurrentPost().id,
          'suggestionHistory': wp.data.select('core/editor').getEditedPostAttribute('meta')['_sb_suggestion_history'], 
          nonce: adminLocalizer.nonce,
        };
      
        jQuery.post(ajaxurl, data, function (response) { // eslint-disable-line  
          
           let suggestionHistoryUpdated = wp.data.select('core/editor').getCurrentPostAttribute('meta')['_sb_suggestion_history'];  
           suggestionHistory = wp.data.select('core/editor').getEditedPostAttribute('meta')['_sb_suggestion_history'];  
          
           // merge current post meta and edited post meta
           if(suggestionHistoryUpdated !== '' && suggestionHistory !== ''){ 
             suggestionHistoryUpdated = _merge( JSON.parse(suggestionHistory) , JSON.parse(suggestionHistoryUpdated) );  
             suggestionHistoryUpdated = JSON.stringify(suggestionHistoryUpdated);   
           }else{
            suggestionHistoryUpdated = suggestionHistory;   
           }  
 
           // update post suggestions meta value, 
           // it send mail multiple times, if we dont update is_draft status in edited meta
           wp.data.dispatch('core/editor').editPost({
                meta: { _sb_suggestion_history: suggestionHistoryUpdated },
           });
 
        });  
      
    }
  }
  
  componentDidMount() { 
    let localUser = localStorage.getItem('sg_userid');
   
    if( this.state.currentUserid !== this.props.userID && parseInt(localUser) !== this.props.userID ){ 
       
      wp.apiFetch( { path: 'wp/v2/users/'+ this.props.userID } ).then( userData => { 
        
        localStorage.setItem("sg_userid", userData.id); 
        localStorage.setItem("sg_username", userData.name);
        localStorage.setItem("sg_role", userData.userRole); 
        let userAvtars = userData.avatar_urls;
        localStorage.setItem("sg_avtarUrl",userAvtars[Object.keys(userAvtars)[1]]); 
         
        this.setState({
          isLoaded: true 
        }); 

      });  

    }else{
      this.setState({
        isLoaded: true 
      });
    }
  }
 
  renderNormalMode() {
    const { action, userID, index, timestamp } = this.props; 
    const { isLoaded } = this.state; 

    // Display the textarea for new comments.
    jQuery('.cls-board-outer.focus .shareCommentContainer').show();

    let owner = wp.data.select("core").getCurrentUser().id;
    let commentVisibility ='block';
    if(index > getCommentsLimit()){
      commentVisibility ='none';
    }

    let str = this.props.title;
    let readmoreStr = '';
    const maxLength = 300;
    if (maxLength < str.replace(/<\/?[^>]+(>|$)/g, "").length) {
        readmoreStr = str;
        str = str.substring(0, maxLength) + '...';
    }
    // Removing contenteditable attr from the link.
    str = str.replace(/contenteditable=\"false\"/ig, 'data-edit="false"'); // eslint-disable-line
  
    let Datetime = convertedDatetime( timestamp );
    let timeAgoDate = timeAgo( timestamp ); 
     
    if ( this.state.currentUserid !== this.props.userID && ! isLoaded) {
      return <div></div>;
    } else {

      let username,  avtarUrl,  role;
      if( this.state.currentUserid !== userID )  {
        username = localStorage.getItem("sg_username");
        avtarUrl = localStorage.getItem("sg_avtarUrl");
        role = localStorage.getItem("sg_role"); 
      }else{
        let userinfo = getCurrentUserInfoById();  
        username = userinfo['username'];
        avtarUrl = userinfo['avtarUrl'];
        role = userinfo['role'];
      }
     
      return (
        <div className="commentContainer"  style={{display:commentVisibility}} id={timestamp}>
          <div className="commentInnerContainer">
          <div className="comment-header">
              <div className="comment-details">
                  <div className="avatar"><img src={  avtarUrl } alt="avatar"/></div>
                  <div className="commenter-name-time">
                      <div className="commenter-name">{ username } <span className="tooltip">{ role }</span></div>
                      <div className="comment-time"><span className="comment-time-wrapper">{timeAgoDate} <span className="tooltip">{ Datetime }</span></span></div>
                  </div>
                  <div className="comment-actions">
                    {index === 0 && <ul className="suggestion-comment-accpt-meta">
                      <li  onClick={this.acceptSuggestion.bind(this)}>
                        <i className="btn-add-suggestion dashicons dashicons-yes"><span className="tooltip">Accept suggestion</span></i>
                      </li>
                      <li  onClick={this.removeSuggestion.bind(this)}>
                        <i className="btn-remove-suggestion dashicons dashicons-no-alt"><span className="tooltip">Reject suggestion</span></i>
                      </li>
                    </ul> }
                    
                    { 'reply' === action && userID === owner &&
                      <div className="buttons-wrapper">
                          <span className="tooltip">More Options...</span>
                          <div className="more-option-btn"></div>
                          <div className="comment-more-option">
                              <ul className="comment-more-option-list">
                                  <li onClick={this.editComment}>
                                      <i className="dashicons dashicons-edit btn btn-comment js-edit-comment"></i><span className="more-option-meta">{'Edit'}</span>
                                  </li>
                                  <li className="js-trash-comment">
                                    <i className="dashicons dashicons-trash btn btn-comment "></i><span className="more-option-meta">{'Delete'}</span>
                                  </li>
                              </ul>
                          </div>
                      </div>
                    }
                </div> 
              </div> 
          </div>
          <div className="commentText">
            <span className='readlessTxt readMoreSpan active' >{this.props.mode.toLowerCase() !== 'reply' && <strong>{this.props.action} :</strong> } {renderHTML(str)} {'' !== readmoreStr && <span className='readmoreComment'>show more</span>}</span>
            <span className='readmoreTxt readMoreSpan'>{this.props.mode.toLowerCase() !== 'reply' && <strong>{this.props.action} :</strong> } {renderHTML(readmoreStr)} {'' !== readmoreStr && <span className='readlessComment'>show less</span>}</span> 
          </div>
          <div className="comment-delete-overlay">
              <span className="comment-overlay-text">Delete this comment?</span>
              <div className="comment-delete-overlay-inner">
                  <button onClick={this.removeComment.bind(this)}  className="btn btn-delete">Delete</button>
                  <button onClick={this.cancelEdit.bind(this)} className="btn btn-cancel">Cancel</button>
              </div>
          </div>
          {undefined !== this.props.editedTime &&
            <time className="updated-time">(edited at {timeAgo(this.props.editedTime)}) <span className = "tooltip">{convertedDatetime( this.props.editedTime )}</span></time>
          } 
          </div>
        </div>
      );
    }
  }

  renderEditingMode() {
    const { timestamp } = this.props; 
    const { isLoaded } = this.state;

    var previousValue = this.props.children[0]; 
    // Filtering anchor tag and return the url text only.
    // For bug fixing of semicolon there is a little chnage in regex
    previousValue = removeLinkFromEditableText(previousValue);
    previousValue = filterTextForEdit(previousValue);

    var newText = this.state.contentHtml;
    newText =  filterTextForEdit(newText);

    let buttonClass = '';
    if( newText === '' || newText === previousValue ){
      buttonClass = 'btn-disabled';
    } 

    // Hide the textarea for new comments.
    jQuery('.cls-board-outer.focus .shareCommentContainer').hide();

    let Datetime = convertedDatetime( timestamp );
    let timeAgoDate = timeAgo( timestamp ); 
 
    if ( this.state.currentUserid !== this.props.userID && ! isLoaded) {
      return <div></div>;
    } else {

      let username,  avtarUrl,  role;
      if( this.state.currentUserid !== this.props.userID )  {
        username = localStorage.getItem("sg_username");
        avtarUrl = localStorage.getItem("sg_avtarUrl");
        role = localStorage.getItem("sg_role"); 
      }else{
        let userinfo = getCurrentUserInfoById();  
        username = userinfo['username'];
        avtarUrl = userinfo['avtarUrl'];
        role = userinfo['role'];
      }
      
      return (
        <div className="commentContainer " id={timestamp}>
          <div className="commentInnerContainer">
          <div className="comment-header">
              <div className="comment-details">
              <div className="avatar"><img src={  avtarUrl } alt="avatar"/></div>
                <div className="commenter-name-time">
                    <div className="commenter-name">{ username } <span className="tooltip">{ role }</span></div>
                      <div className="comment-time">{timeAgoDate} <span className="tooltip">{ Datetime }</span></div>
                  </div>
              </div>
          </div>
          <div className="commentText">
              <div className="cf-share-comment-wrapper js-cf-share-comment-wrapper">
                          <ContentEditable
                              innerRef={ this.contentEditable }
                              html= {this.state.contentHtml}
                              disabled={ false }
                              onChange={  ( e ) =>  this.setState( { contentHtml: e.target.value } ) }
                              id={ `edit-${this.props.timestamp}` }
                              className="cf-share-comment js-cf-edit-comment"
                              placeholder="Edit your comments..."
                              
                          />
              </div> 
          </div>
          <div className="comment-delete-overlay">
              <span className="comment-overlay-text">Delete this comment?</span>
              <div className="comment-delete-overlay-inner">
                  <button onClick={this.removeComment.bind(this)}  className="btn btn-delete">Delete</button>
                  <button onClick={this.cancelEdit.bind(this)} className="btn btn-cancel">Cancel</button>
              </div>
          </div>
          <button onClick={this.saveComment.bind(this)} className={`btn-comment save-btn ${buttonClass}`} >
            {'Save'}
            <span className="tooltip">{ 'Save changes' }</span>
          </button>
          <button onClick={this.cancelEdit.bind(this)} className="btn-comment js-cancel-comment">
            {'Cancel'}
            <span className="tooltip">{ 'Discard changes' }</span>
          </button>
          </div>
        </div>
      );
    }
  }

  render() {  
    return this.state.editing ? this.renderEditingMode() : this.renderNormalMode();
  }
}
 