import SuggestionComment from "./suggestion-comment";
import ContentEditable from 'react-contenteditable';
const {Fragment} = wp.element;
const {__} = wp.i18n;  // eslint-disable-line
const isSafari = /^((?!chrome|android).)*safari/i.test(navigator.userAgent);

export default class SuggestionBoard extends React.Component {

  constructor(props) {

    super(props);
    this.contentEditable = React.createRef();
    this.displayComments = this.displayComments.bind(this);
    this.updateComment = this.updateComment.bind(this);
    this.removeComment = this.removeComment.bind(this);
    this.addNewComment = this.addNewComment.bind(this);
    this.cancelComment = this.cancelComment.bind(this);
    
    this.state = {suggestedOnText: this.props.suggestedOnText ,newcommentText: ''};
 
  }

  removeComment( index ) {
    const { suggestionID } = this.props;
    const timestamp= getTimestampWithTimezone();
    let currentBoardSuggestion = this.state.suggestedOnText;
    let suggestionHistory = wp.data.select( 'core/editor' ).getEditedPostAttribute( 'meta' )['_sb_suggestion_history'];
    suggestionHistory = JSON.parse(suggestionHistory);
    currentBoardSuggestion[index]['status'] = 'deleted';
    suggestionHistory[suggestionID] = currentBoardSuggestion;
    suggestionHistory[suggestionID][0]['is_draft']=true;
    suggestionHistory[suggestionID][0]['updated_at']=timestamp;
    wp.data.dispatch('core/editor').editPost({meta: {_sb_suggestion_history: JSON.stringify(suggestionHistory) } });
    //currentBoardSuggestion.splice(index, 1); 
    this.setState({suggestedOnText: currentBoardSuggestion});
  }

  updateComment(newText, index ) {
    const { suggestionID } = this.props;
    const timestamp = getTimestampWithTimezone();
    let currentBoardSuggestion = this.state.suggestedOnText;
    let suggestionHistory = wp.data.select( 'core/editor' ).getEditedPostAttribute( 'meta' )['_sb_suggestion_history'];
    suggestionHistory = JSON.parse(suggestionHistory);
    currentBoardSuggestion[index]['text'] = newText; 
    currentBoardSuggestion[index]['editedTime'] = timestamp; 
    suggestionHistory[suggestionID] = currentBoardSuggestion;
    suggestionHistory[suggestionID][0]['is_draft']=true;
    suggestionHistory[suggestionID][0]['updated_at']=timestamp;
    wp.data.dispatch('core/editor').editPost({meta: {_sb_suggestion_history: JSON.stringify(suggestionHistory) } });
    this.setState({suggestedOnText: currentBoardSuggestion});
  }

  addNewComment(event) {
    event.preventDefault();
    const { suggestionID } = this.props;
    let currentTextID = 'txt' + suggestionID;
    var newText = jQuery('#' + currentTextID).html();
    newText =  filterTextBeforeSave(newText); 

    let newTextstring =  validateCommentReplyText(newText); 
    
    if ( '' !== newText && '' !== newTextstring && /\S/g.test(newTextstring)) {
      const currentUser = getCurrentUserId(); 
      const timestamp= getTimestampWithTimezone();
      let suggestionHistory = wp.data.select( 'core/editor' ).getEditedPostAttribute( 'meta' )['_sb_suggestion_history'];
      suggestionHistory = JSON.parse(suggestionHistory); 
      let newCommentInfo = {'uid': currentUser, 'action': 'reply', 'mode': 'Reply', 'text': newText, 'is_draft': true, 'timestamp':timestamp, 'status':'publish'};
      suggestionHistory[suggestionID].push(newCommentInfo);
      suggestionHistory[suggestionID][0]['is_draft']=true;
      suggestionHistory[suggestionID][0]['updated_at']=timestamp;
      wp.data.dispatch('core/editor').editPost({meta: {_sb_suggestion_history: JSON.stringify(suggestionHistory) } });
      jQuery('#' + currentTextID).html('').focus();
      this.setState({suggestedOnText: suggestionHistory[suggestionID] , newcommentText: ''});
      
    } else {
      if(jQuery(event.currentTarget).hasClass('btn-disabled')){
        return false;
      }
      wp.data.dispatch( 'core/notices' ).createNotice(
        'error', // Can be one of: success, info, warning, error.
        __('Please write a comment to share!'),// Text string to display.
        {
            id: 'commenttoshare',
            isDismissible: true, // Whether the user can dismiss the notice.
            // Any actions the user can perform. 
        }
      ); 
    }

    if( jQuery( `#sg${suggestionID} .cf-assign-to` ).length ) {
      jQuery( `#sg${suggestionID} .cf-assign-to` ).remove();
    }
    jQuery( `#sg${suggestionID}`).find(".btn-success").addClass('btn-disabled');
  }

  cancelComment() { 
    const { suggestionID } = this.props;
    let currentTextID = 'txt' + suggestionID;
    jQuery('#' + currentTextID).html('');
    // Reset Comments Float.
    jQuery('#md-span-comments .cls-board-outer').removeClass('focus');
    jQuery('#md-span-comments .cls-board-outer').removeAttr('style');
    jQuery('[data-rich-text-format-boundary]').removeAttr('data-rich-text-format-boundary');
                  
    // Removing the active class form activity center on cancel.
    jQuery( '.js-activity-centre .user-data-row' ).removeClass( 'active' );
    this.setState( { newcommentText: '' } );
    jQuery.event.trigger({ type: "showHideComments"});
  }

  displayComments(data, i) {
    const { suggestionID } = this.props;

    var status= data.status ? data.status : '';
    if(status === 'deleted'){
      return;
    }
      
    return (
      <SuggestionComment
        key={i}
        index={i}
        removeCommentFromBoard={this.removeComment}
        updateCommentFromBoard={this.updateComment} 
        dateTime={data.time} 
        status={status}
        timestamp={data.timestamp}
        userID={data.uid}
        action={data.action}
        suggestionID={suggestionID}
        clientId={this.props.clientId}
        mode={data.mode}
        title={data.text}
        editedTime={data.editedTime}
      >
        { 'reply' === data.action &&
          data.text
        }
        { 'reply' !== data.action &&
          <Fragment>
            <strong>{data.action}: </strong>
            { data.text.length > 100 ? data.text.substring(0,100) + '...' : data.text}
          </Fragment>
        }
      </SuggestionComment> 
    );

  }
   
  render() {
    const { suggestionID } = this.props;
     
    let commentCount = this.state.suggestedOnText.length;
    let commentToggleVisibility = (commentCount > getCommentsLimit()) ? 'block' : 'none';
    
    var newText = this.state.newcommentText;
    newText = validateCommentReplyText(newText);

    let buttonClass = ( newText === '' ) ? 'btn-disabled' : '';
    
    return (
      <div className={"board sg-board " + this.state.suggestedOnText[0].action}>
        <div className="boardTop">
          {
            this.state.suggestedOnText && this.state.suggestedOnText.map((item, index) => { 
              return this.displayComments(item, index);
            })
          }
          <div className="show-all-comments"  style={{display:commentToggleVisibility}} >show all {commentCount - 1} replies</div>
        </div>

        <div className="shareCommentContainer">
          <div className="cf-share-comment-wrapper js-cf-share-comment-wrapper">
                          <ContentEditable
                              innerRef={ this.contentEditable }
                              html= {this.state.newcommentText}
                              disabled={ false }
                              onChange={  ( e ) =>  this.setState( { newcommentText: e.target.value } ) }
                              id={"txt" + suggestionID}
                              className="cf-share-comment js-cf-share-comment"
                              placeholder="Reply or add others with @" 
                          /> 
             </div>  
          <button onClick={this.addNewComment} className={`btn btn-success ${buttonClass}`}>Reply<span className="tooltip">Reply to comment</span></button>
          <button onClick={this.cancelComment} className="btn btn-cancel">Cancel <span className="tooltip">Discard Comment</span></button>
        </div>
      </div>
    );
  }
}