// Window Load functions.
$(window).on('load', function () {
    let loadAttempts = 0;

    const loadComments = setInterval(function () {
        loadAttempts++;
        if (1 <= $('.block-editor-writing-flow').length) {
            // Clearing interval if found.
            clearInterval(loadComments);

            // Fetching comments
            fetchComments();


        }

        setTimeout(function () {
            $.event.trigger({ type: "showHideComments" });
        }, 1000);
        jQuery.event.trigger({ type: "editorLayoutUpdate" });
        // Clearing interval if not found in 10 attemps.
        if (loadAttempts >= 10) {
            clearInterval(loadComments);
        }
    }, 1000);

    $(document).on('click', '.components-notice__action', function () {

        if ('Restore the backup' === $(this).text()) {

            setTimeout(function () {
                // Sync popups with highlighted texts.
                $('.wp-block mdspan').each(function () {
                    var selectedText = $(this).attr('datatext');
                    if ($('#' + selectedText).length === 0) {
                        createBoard(selectedText, 'value', 'onChange');
                    }
                });
            }, 500);
        }
    });

});

function fetchComments() {
    var parentNode = document.createElement('div');
    parentNode.setAttribute("id", 'md-comments-suggestions-parent');

    var referenceNode = document.querySelector('.block-editor-writing-flow');
    if (null !== referenceNode) {
        referenceNode.appendChild(parentNode);
  
        var commentNode = document.createElement('div');
        commentNode.setAttribute("id", 'md-span-comments');
        commentNode.setAttribute("class", 'comments-loader');
        var parentNodeRef = document.getElementById('md-comments-suggestions-parent');
        parentNodeRef.appendChild(commentNode);

        var allThreads = [];
        var selectedNontextblock = [];
        var selectedTexts = [];
       

        /* <fs_premium_only> */
        let suggestionHistory;

        if (multicollab_fs.can_use_premium_code) {
            if (multicollab_fs.is_plan_pro) {
                suggestionHistory = wp.data.select('core/editor').getEditedPostAttribute('meta')['_sb_suggestion_history'];

                if (suggestionHistory && 0 < suggestionHistory.length) {
                    suggestionHistory = JSON.parse(suggestionHistory);
                } else {
                    suggestionHistory = {};
                }
            }
        }
        /* </fs_premium_only> */

        const queryString = window.location.search;
        const urlParams = new URLSearchParams(queryString);
        const current_url = urlParams.get('current_url');
        let selectedDataText;
        //nonTextBlock datatext attribute
        $('.commentIcon, .wp-block mdspan, .wp-block .mdadded, .wp-block .mdmodified, .wp-block .mdremoved').each(function () {
            if ($(this).hasClass('mdadded') || $(this).hasClass('mdremoved') || $(this).hasClass('mdmodified')) {
                selectedDataText = $(this).attr('id');
            } else {
                selectedDataText = $(this).attr('datatext');
            }
            selectedNontextblock.push(selectedDataText)
        });
        // If no comment tag exist, remove the loader and temp style tag immediately.
        const span_count = $('.wp-block mdspan').length;
        const nontext_count = selectedNontextblock.length;

        /* SGEDIT */
        let suggestion_count;
        if (multicollab_fs.can_use_premium_code) {
            if (multicollab_fs.is_plan_pro) {
                suggestion_count = $('.wp-block .mdadded,.wp-block .mdmodified,.wp-block .mdremoved').length;
            } else {
                suggestion_count = 0;
            }
        } else {
            suggestion_count = 0;
        }
        /* SGEDIT */

        if (0 === span_count && 0 === nontext_count && 0 === suggestion_count) {
            $('.commentOn .block-editor-writing-flow').css({ width: '100%' })
            //$('body').removeClass("commentOn");
            jQuery.event.trigger({ type: "editorLayoutUpdate" });
            $('#md-span-comments').removeClass('comments-loader');
            $('#loader_style').remove();
            //update autodraft meta data
            updateAutodraftIds('');
            if (current_url) {
                wp.data.dispatch('core/notices').createNotice(
                    'error', // Can be one of: success, info, warning, error.
                    __('Your Comment/Suggestion is not Exist!'),// Text string to display.
                    {
                        id: 'deletecopyurl',
                        isDismissible: true, // Whether the user can dismiss the notice.
                        // Any actions the user can perform.

                    }
                );
                // alert('Your Comment is Deleted or Resolved! Please check with different URL');
                urlParams.delete('current_url');
                window.history.replaceState({}, '', `${location.pathname}?${urlParams}`);
            }
        } else {
            //$('body').addClass("commentOn");

            jQuery.event.trigger({ type: "editorLayoutUpdate" });
            selectedTexts = selectedNontextblock;
            selectedTexts = selectedTexts.filter(function (e) { return e });

            selectedTexts.forEach(selectedText => {

                if ('undefined' !== selectedText && $('#' + selectedText).length === 0 && selectedText.match(/^el/m) !== null) {

                    var newNode = document.createElement('div');
                    newNode.setAttribute("id", selectedText);
                    newNode.setAttribute("class", "cls-board-outer cm-board is_active");

                    var referenceNode = document.getElementById('md-span-comments');
                    referenceNode.appendChild(newNode);

                    ReactDOM.render(
                        <Board datatext={selectedText} onLoadFetch={1} />,
                        document.getElementById(selectedText)
                    )
                    allThreads.push(selectedText);
                }
                /* <fs_premium_only> */
                if (multicollab_fs.can_use_premium_code) {
                    if (multicollab_fs.is_plan_pro) {

                        if (suggestionHistory !== undefined && suggestionHistory !== null && Object.keys(suggestionHistory).length !== 0 && selectedText.match(/^el/m) === null && $('#sg' + selectedText).length === 0) {
                            if (!Object.keys(suggestionHistory).includes(selectedText)) {
                                return;
                            }

                            let boardClass = 'Delete' === suggestionHistory[selectedText][0].mode ? 'cls-board-outer Delete' : 'cls-board-outer';
                            boardClass = boardClass + ' sg-board';
                            let newNode = document.createElement('div');
                            newNode.setAttribute('id', 'sg' + selectedText);
                            newNode.setAttribute('data-sid', selectedText);
                            newNode.setAttribute('class', boardClass);
                            commentNode.appendChild(newNode);

                            let clientId = $('#' + selectedText).parents('.wp-block')[0].getAttribute('data-block');

                            ReactDOM.render(
                                <SuggestionBoard
                                    clientId={clientId}
                                    suggestionID={selectedText}
                                    suggestedOnText={suggestionHistory[selectedText]} />,
                                document.getElementById('sg' + selectedText)
                            );

                            allThreads.push('sg' + selectedText);
                        }
                    }
                }
                /* </fs_premium_only> */


            });
            //update autodraft meta data
            updateAutodraftIds(allThreads);
            const copyDatatext = allThreads.includes(current_url);
            if (current_url && false === copyDatatext) {
                wp.data.dispatch('core/notices').createNotice(
                    'error', // Can be one of: success, info, warning, error.
                    __('Your Comment/Suggestion is not Exist!.'),// Text string to display.
                    {
                        id: 'deleteresolvedcopyurl',
                        isDismissible: true, // Whether the user can dismiss the notice.
                        // Any actions the user can perform.

                    }
                );
                urlParams.delete('current_url');
                window.history.replaceState({}, '', `${location.pathname}?${urlParams}`);
            } else {
                $('.js-activity-centre .user-data-row').removeClass('active');
                $(`#cf-${current_url}`).addClass('active');

                if (current_url && current_url.match(/^sg/m) !== null) {

                    wp.data.dispatch('core/editor').editPost({ meta: { _sb_show_suggestion_boards: false } });
                    $('.suggestion-toggle .components-form-toggle').removeClass('is-checked');
                    $('body').removeClass('hide-sg');
                    $('#' + current_url).trigger('click');


                }

            }

            let loadAttempts = 0;
            const loadComments = setInterval(function () {
                //remove Visible
                var openBoards = $('.cls-board-outer').length;
                if (0 === openBoards) {
                    jQuery.event.trigger({ type: "editorLayoutUpdate" });
                    $('#md-span-comments').removeClass('comments-loader');
                }
                loadAttempts++;
                if (1 <= $('#md-span-comments .commentContainer').length) {
                    clearInterval(loadComments);
                    $('#loader_style').remove();
                    $('#md-span-comments').removeClass('comments-loader');
                    //Remove Visible
                    $('#history-toggle').attr('data-count', $('.cls-board-outer').length);
                }
                if (loadAttempts >= 10) {
                    clearInterval(loadComments);
                    $('#loader_style').remove();
                    $('#md-span-comments').removeClass('comments-loader');
                }
            }, 1000);
        }
    }
}
// Float comment for  Non Text block Board
$(document).on('click', '.commentIcon', function () {
    var block = wp.data.select('core/block-editor').getSelectedBlock();
    var focusElement = document.activeElement.localName;
    var selectedText;

    if (allowedBlocks.includes(block.name)) {
        selectedText = $('#block-' + block.clientId).attr('datatext');
        wp.data.dispatch('mdstore').setDataText(selectedText);
        if ('figcaption' === focusElement) {
            return;
        }

    } else {
        return;
    }


    if ('undefined' !== selectedText) {
        $('.cls-board-outer').removeClass('focus');
        $('.cls-board-outer').removeClass('is-open');
        $('#md-span-comments .comment-delete-overlay').removeClass('show');
        $('#md-span-comments .comment-resolve .resolve-cb').prop("checked", false);
        $('.cls-board-outer .buttons-wrapper').removeClass('active');
        $('#' + selectedText + '.cls-board-outer').addClass('focus');
        $('#md-span-comments .cls-board-outer').css('opacity', '0.4');
        $('#md-span-comments .cls-board-outer.focus').css('opacity', '1');
        $('#md-span-comments .cls-board-outer').css('top', 0);
        $('mdspan').removeAttr('data-rich-text-format-boundary');
        var findMdSpan = '.cls-board-outer';
        $(findMdSpan).each(function () {
            var boardDatatext = $(this).attr('id');
            if (boardDatatext.includes(selectedText)) {
                let topOfText = $('[datatext="' + selectedText + '"]').offset().top;
                $('#' + selectedText).offset({ top: topOfText });
                scrollBoardToPosition(topOfText);
                //Adding class to prevent multiple click on same selected Text
                $('#' + selectedText + '.cls-board-outer').addClass('is-open');
                $('.js-activity-centre .user-data-row').removeClass('active');
                $(`#cf-${selectedText}`).addClass('active');
                wp.data.dispatch('mdstore').setIsActive(true);

            }
            if (!boardDatatext.includes(selectedText)) {
                removeBlankPopup(selectedText);
            }

        });

        jQuery.event.trigger({ type: "showHideComments" });
    }
})

// Delete the popup and its highlight if user
// leaves the new popup without adding comment.
$(document).on('click', '.is-selected', function (e) {
    var block = wp.data.select('core/block-editor').getSelectedBlock();
    if (null != block) {
        var focusedElement = document.activeElement.localName;
        const latestBoard = $('.board.fresh-board').parents('.cls-board-outer').attr('id');
        var selectedText = $('#block-' + block.clientId).attr('datatext');
        if ('figure' === focusedElement || 'video' === focusedElement || 'img' === focusedElement) {

            $('.toolbar-button-with-nontext').css('display', 'block');
            $('.toolbar-button-with-text').css('display', 'none');
        }
        if (1 === $('.board.fresh-board').length && 0 === $('.board.fresh-board .loading').length) {
            removeBlankPopup(selectedText);
        }
        if (allowedBlocks.includes(block.name) && $(this).hasClass('commentIcon')) {
            if (undefined === selectedText) {
                wp.data.dispatch('mdstore').setIsActive(false);
                $('#md-span-comments .cls-board-outer').css('opacity', '1');
                $('#md-span-comments .cls-board-outer').removeClass('focus');
                $('#md-span-comments .cls-board-outer').removeClass('is-open');
                $('#md-span-comments .cls-board-outer').removeAttr('style');
                $('#md-span-comments .comment-delete-overlay').removeClass('show');
                $('#md-span-comments .comment-resolve .resolve-cb').prop("checked", false);
                $('#md-span-comments .cls-board-outer .buttons-wrapper').removeClass('active');
            }
        }

    }
})
